<template>
  <div class="module-type9">
    <section>
      <div class="gift">
        <div
          class="gift-book"
          v-for="it in moduleObj.albumList"
          :key="it.programId"
          @click="getAllData(it.linkUrl)"
        >
          <div class="gift-book-img">
            <img :src="it.programImg" />
          </div>
          <p>{{ it.programTit }}</p>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
export default {
  name: "ModuleType9",
  props: {
    moduleObj: Object,
  },
  methods: {
    getAllData(url) {
      this.$router.push({
        path: "/shopping-page",
        query:{
          url
        }
      });
    },
  },
};
</script>
